@import "../../sass/settings/_mixins"
@import "../../sass/settings/_vars"

.header
    position: fixed
    z-index: 2
    top: 0
    left: 0
    width: 100%
    padding: 10px 0
    //background: $mainColor
    backdrop-filter: blur(10px)
    .header-container
        max-width: 1230px
        margin: 0 auto 
        padding: 0 15px
        display: flex
        align-items: center
        justify-content: space-between
        .header-logo
            height: 40px
            width: 40px
            flex: 0 0 40px
            border-radius: 50%
            transition: all 0.2s linear
            position: relative
            z-index: 6
            margin-right: 10px
            .header-logo-label
                position: absolute
                top: 12px
                left: 50px
                text-transform: uppercase
                color: $colorWhite
            &::before,&::after,span 
                position: absolute
                border-radius: 2px
                pointer-events: none
                background: $mainColor
            span 
                top: 3px
                left: 15px
                height: 30px
                width: 4px
                transform: rotate(35deg)
            &::after 
                content: ""
                height: 4px
                width: 18px
                bottom: 9px
                left: 8px
            &::before 
                content: ''
                width: 8px
                height: 4px
                top: 16px
                left: 16px
        .header-nav
            .header-nav-list
                .header-nav-list-item
                    a
                        text-transform: uppercase
                        padding: 5px 0
                        color: #7e7e7e
                        &.active
                            color: $colorWhite
        .header-menu-icon
            background: transparent
            position: relative
            z-index: 6
            width: 30px
            height: 24px
            span,&:before,&:after
                pointer-events: none
                position: absolute
                left: 0
                width: 100%
                height: 2px
                background: $colorWhite
                transition: all .4s ease
            span
                top: calc( 50% - 1px)
            &:before,&:after
                content: ''
            &:before
                top: 0px
            &:after
                bottom: 0px

@media( min-width: $tablet )
    .header
        .header-logo-label
            @include adaptiv-property(font-size, 18 , 16, 1230, 992 )
            width: 130px
        .header-nav-list-item
            a
                @include adaptiv-property(font-size, 16 , 12, 1230, 992 )

@media( max-width: $tablet )
    .header
        .header-logo-label
            font-size: 16px
            width: 120px
        .header-nav-list-item
            a
                font-size: 12px

@media( min-width: $mobile)
    .header
        .header-nav
            .header-nav-list
                @include flex-center
                column-gap: 20px
        .header-menu-icon
            display: none

@media( max-width: $mobile)
    .header
        .header-nav
            position: fixed
            z-index: 3
            top: 0
            left: 0
            width: 100%
            height: 100vh
            background: $mainColor
            transform: translateX(-120%)
            transition: all .4s ease
            .header-nav-list
                padding-top: 120px
                padding-left: 30px
                @include flex-column
                row-gap: 30px
                .header-nav-list-item
                    a
                        display: block
                        font-size: 20px
                        opacity: 0
                        transform: translateY(10px)
                        transition: all 0.5s ease 0.5s
        .header-menu-icon
            display: block


.header.active
    .header-nav
        transform: translateX(0)
        .header-nav-list
            .header-nav-list-item
                a
                    opacity: 1
                    transform: translateY(0px)
    .header-menu-icon
        span
            display: none
        &:before
            transform: rotate(45deg) translateX(15px) 
            left: -10px
        &:after
            transform: rotate(-45deg) translateX(15px) 
            left: -10px






