@import "../../sass/settings/_mixins"
@import "../../sass/settings/_vars"

.weatherPage
    color: $colorWhite
    .weatherPage-container
        .weatherPage-link-back
            @include flex-center
            color:$colorWhite
            transition: all .3s ease
            &::before
                padding-right: 5px
                transition: all .3s ease
            &:hover
                opacity: 1
                &::before
                    transform: translateX(-5px)
        .weather-city-name
            font-weight: 700
            margin-bottom: 20px
        .weatherPage-links
            @include flex-center
            .weatherPage-link
                padding: 5px 0
                font-weight: 500
                color: #a0a0a0
                padding-bottom: 3px
                position: relative
                z-index: 0
                &::after
                    content:''
                    position: absolute
                    bottom: -3px
                    left: -10%
                    width: 0%
                    height: 1px
                    background: $colorWhite
                    transition: all .3s ease
                &:first-child
                    &::before
                        display: none
                &:hover
                    color: $colorWhite
                    &::after
                        width: 120%
            .weatherPage-link.active
                color: $colorWhite
                &::after
                    width: 120%
        .weather-info
            .weatherPage-header
                display: flex
                column-gap: 5px
                .weather-status
                    @include flex-center
                    font-weight: 500
                    .wheather-icon
                        padding-top: 10px
                        img
                            width: 100%
                            height: 100%
                            object-fit: contain
            .weatherPage-body
                @include flex-column

.weatherPage-favorite
    padding-top: 30px
    .weatherPage-favorite-container
        @include flex-column
        row-gap: 20px
        .weatherPage-favorite-title
            font-size: 20px
            margin-bottom: 10px


.weather-city
    max-width: 100%
    color: $colorWhite
    box-shadow: 0px 5px 10px  0 #000
    padding: 30px 20px
    display: flex
    align-items: center
    justify-content: space-between
    transition: all .3s ease
    &:hover
        box-shadow: 0px 5px 20px  0 #000
    .weather-header
        flex: 0 0 33.33%
        display: flex
        align-items: center
        .weather-main
            display: flex
            column-gap: 30px
            .weather-city-name
                position: relative
                @include flex-center
                flex-direction: row-reverse
                color: $colorWhite
                text-transform: uppercase
                font-weight: 600
                &::before
                    margin-bottom: 3px
                    transform: rotate(180deg)  translateX(15px)
                    opacity: 0
                    transition: all 0.3s ease 0.2s
                &::after
                    content: ''
                    position: absolute
                    bottom: -4px
                    left: 0
                    width: 0%
                    height: 2px
                    background: $colorWhite
                    transition: all 0.3s ease 
                &:hover
                    &::before
                        opacity: 1
                        transform: rotate(180deg)  translateX(-10px)
                    &::after
                        width: 100%
            .weather-status
                font-size: 14px
        .wheather-icon
            img
                width: 100%
                height: 100%
                object-fit: contain
    .wheather-temp
        display: flex
        span
            text-transform: uppercase
            font-weight: 600
            &::after
                content: ""
                display: inline-block
                width: 8px
                height: 8px
                margin: 0 0 15px 10px
                border-radius: 50%
                border: 2px solid #fff
        &::after
            padding-top: 5px
            content: 'C'
            text-transform: uppercase
            font-weight: 600
            color:#fff
    .weather-feels-like
        position: relative
        span
            font-weight: 600
            padding-left: 10px
            &::after
                content: ""
                display: inline-block
                width: 10px
                height: 10px
                border-radius: 50%
                border: 3px solid #fff
                margin: 0 0 15px 10px
        &::after
            content: 'C'
            font-weight: 600
            color:#fff
    .weather-loading
        width: 100px
        height: 100px
        position: relative
        img
            width: 100%
            height: 100%
            object-fit: contain
    .weather-favorite
        width: 30px
        height: 30px
        flex: 0 0 30px
        background: transparent
        &::before   
            color: #0000005a
            transition: all .3s ease
        &.favorite
                &::before
                    opacity: 1
                    color: #f30000


._weather-info
    @include flex-center
    column-gap: 10px
    color: #b5b3b3
    span
        &:first-child
            column-gap: 10px
            color: #b5b3b3
        &:last-child
            font-weight: 500
            color: $colorWhite

._temp
    display: flex
    column-gap: 10px
    span
        &:last-child
            &::after
                content: ""
                display: inline-block
                width: 8px
                height: 8px
                margin: 0 0 7px 10px
                border-radius: 50%
                border: 2px solid #fff
        &:first-child
            &::after
                display: none
    &::after
        content: 'C'
        margin-left: -7px
        text-transform: uppercase
        font-weight: 600
        color: $colorWhite






@media( min-width: $tablet )   
    .weatherPage
        padding: 30px 0
        .weatherPage-container
            .weatherPage-link-back
                font-size: 14px
                &::before
                    font-size: 12px
            .weather-city-name
                margin-top: 30px
                font-size: 34px
                margin-bottom: 20px
            .weatherPage-links
                @include flex-center
                column-gap: 20px
                .weatherPage-link
                    font-size: 16px
                    &::before
                        content: ''
                        display: block
                        width: 1px
                        height: 15px
                        position: absolute
                        top: 5px
                        left: -10px
                        background: $colorWhite
                        opacity: 0.6
            .weather-info
                margin-top: 20px
                .weatherPage-header
                    .weather-status
                        column-gap: 30px
                        font-size: 22px
                        .wheather-icon
                            margin-bottom: 10px
                            width: 60px
                            height: 60px
                .weatherPage-body
                    @include flex-column
                    row-gap: 20px
    .weather-city
        .weather-header
            .weather-main
                column-gap: 30px
                .weather-city-name
                    font-size: 22px
                    min-width: 200px
                    justify-content: start
                    &::before
                        font-size: 14px
                .weather-status
                    min-width: 100px
                    font-size: 14px
                    margin-right: 10px
            .wheather-icon
                width: 50px
                height: 50px
        .wheather-temp
            span
                font-size: 32px
            &::after
                font-size: 24px
        .weather-feels-like
            font-size: 16px
            span
                padding-left: 10px
                font-size: 32px
            &::after
                font-size: 24px
                color:#fff
        .weather-favorite
            width: 40px
            height: 40px
            font-size: 26px
            &:hover
                &::before
                    
                    opacity: 0.7
                    color: #f30000
    ._weather-info
        span
            &:first-child
                font-size: 16px
                min-width: 220px
            &:last-child
                font-size: 20px
    ._temp
        display: flex
        column-gap: 10px
        span
            font-size: 25px
        &::after
            font-size: 24px

